.header {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-top: 30px;
}

.left,
.right {
    writing-mode: vertical-lr;
    writing-mode: sideways-lr;
    color: rgba(179, 109, 30, 1);
    width: 50%;
    height: 12rem;
    text-align: center;
}

.left h2,
h1 {
    position: relative;
    left: 20%;
    font-size: 1.8rem;
    letter-spacing: .1rem;
}

.left h1 {
    font-size: 2.5rem;
    top: 3rem;
}

.right span {
    position: relative;
    display: block;
    width: 80%;
    height: 0;
    padding-top: 80%;
    left: 10%;
}

.right img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}