.item {
    position: relative;
    border-top-left-radius: 15%;
    border-bottom-right-radius: 15%;
    overflow: hidden;
}

.item label {
    z-index: 10;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    writing-mode: vertical-lr;
    font-size: 1rem;
    font-weight: bold;
    letter-spacing: 1px;
    height: 100%;
    text-align: center;
}