    color: rgba(179, 109, 30, 1);
    font-weight: bold;
}
.articlecontent {
    text-indent: 0rem;
    padding: 10px;
    padding-bottom: 3rem;
}

.articlecontent p {
    padding: 1rem;
    letter-spacing: 1px;
    line-height: 1.5rem;
    font-size: 14px;
}

img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.hide {
    display: none;
}

.nav {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
}


/* Hmong.jsx */
.title {
    color: rgba(252, 219, 129, 1);
    font-size: 20px;
    font-weight: 400;
    word-spacing: 1px;
    line-height: auto;
    padding: 20px;
}

.pagetitle {
    color: #fff;
    position: relative;
    left: -10px;
    width: calc(100% + 20px);
    height: 700px;
    padding-top: 4rem;
    z-index: -1;
}

.pagetitle::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: #0000002f;
    top: 0;
    left: 0;
}

.pagetitle .word {
    position: relative;
    height: 350px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    z-index: 1;
}

.pagetitle .pt {
    padding: 0 2rem 1rem 2rem;
}

.pagetitle .pt h3 {
    font-size: 1.5rem;
    padding-bottom: 1rem;
}

.pagetitle .pt h1 {
    font-size: 2.5rem;
    left: 0;
}

.icon-box {
    height: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content .identity {
    text-align: center;
    line-height: 1.75rem;
    color: rgba(179, 109, 30, 1);
    font-weight: bold;
    margin-top: 4rem;
}

.content .imgbox {
    width: 350px;
    height: 500px;
    margin: 1rem auto;
    padding: 0 .5rem;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
}
.content .imgbox img {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background-color: #a0a0a0;
}

.content .imgbox span {
    width: 75%;
    height: 80%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #a0a0a0;
    border-radius: 5px;
}

.content .imgbox span::before {
    content: "2018";
    font-size: 3rem;
    letter-spacing: .2rem;
    position: absolute;
    top: -2rem;
    left: -2.5rem;
    font-weight: bold;
    color: rgba(179, 109, 30, 1);
    z-index: 1;
}

.bussiness {
    padding-top: 2rem;
    background: linear-gradient(180deg, rgba(255, 211, 89, 0.01), rgba(255, 223, 153, 0.7));
    padding-bottom: 5rem;
    overflow: hidden;
}

.my-bus {
    text-align: center;
    color: rgba(179, 109, 30, 1);
    line-height: 2rem;
}

.navig {
    display: flex;
    justify-content: center;
}
.navig span {
    padding: .2rem .8rem;
    display: inline-block;
    border: 1px solid #000;
    margin: 0 .5rem;
    border-radius: 5px;
    font-weight: bolder;
}
.navig span:active {
    background-color: rgba(179, 109, 30, 1);
    animation: background-color .3s ease 0 ;
}


/* Mountain.jsx */
.imgdesc {
    width: 100%;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.imgdesc h1 {
    font-size: 2rem;
    color: #fff;
    writing-mode: vertical-lr;
    writing-mode: sideways-lr;
    z-index: 1;
    height: 450px;
    margin: 0 auto;
    left: 0;
    text-align: center;
    vertical-align: middle;
}
.imgdesc p {
    z-index: 1;
    font-size: medium;
    text-align: center;
}

#mountain div.icon-box {
    height: 200px;
}

/* About */
#about::before {
    background: linear-gradient(180deg, rgba(117, 96, 21, 0.72) 0, rgba(84, 61, 13, 0.01) 30%);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: '';
    z-index: 1;
}

#about div.icon-box {
    height: 200px;
}

#aboutContent div.identity {
    margin: 0 auto;
    padding: 2rem 0;
    font-size: .8rem;
    background-color: rgba(245, 215, 159, 0.85);
    position: relative;
}
#aboutContent div.identity::after {
    content: '';
    background: url(https://img.js.design/assets/img/6368b9925d43d533589ccea3.webp#7e001dc4cf22379892f58df17d46315a);
    top: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
}
#aboutContent .secTitl {
    padding: 2rem 0;
}
#aboutContent .titlIcon {
    height: 60px;
    position: relative;
    opacity: 0.5;
    text-align: center;
}
#aboutContent .titlIcon span {
    height: 100%;
    width: auto;
    display: inline-block;
}
#aboutContent .titlIcon img {
    position: relative;
}

#aboutContent ul {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    font-size: .8rem;
    font-weight: bold;
    list-style-type: none;
    width: 90%;
}
#aboutContent ul li {
    line-height: 1.5rem;
    letter-spacing: .1rem;
    padding: 0 .5rem;
}
#aboutContent .suptlist li {
    position: relative;
    display: grid;
    grid-template-columns: 30% 70%;
    padding: 1.5rem 0 1rem;
    border-top: 2px solid rgba(179, 109, 30, 1);
}
#aboutContent .suptlist li .suptimg {
    position: relative;
    width: 100%;
    height: 100%;
}
#aboutContent .suptlist .suptimg span {
    display: inline-block;
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
}
#aboutContent .suptlist .suptinner {
    padding: 1rem;
}
#aboutContent .sechead {
    width: 100%;
    background: rgba(161, 145, 114, 1);
    border-top-left-radius: 3rem;
    border-top-right-radius: 3rem;
    height: 5rem;
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    padding-bottom: 1rem;
}
#aboutContent .sechead a {
    text-decoration: none;
    color: #fff;
    padding: .5rem 1rem;
    border-radius: 1rem;
}
#aboutContent .sechead a.active {
    background-color: rgba(235, 223, 199, 1);
    color: #000;
}
#aboutContent .secbody {
    background-color: rgba(161, 145, 114, 1);
    padding: 2rem 1rem;
}
#aboutContent .expertlist, .patentlist, .unilist {
    overflow: auto;
}
#aboutContent .expertlist::-webkit-scrollbar,
#aboutContent .patentlist::-webkit-scrollbar,
#aboutContent .unilist::-webkit-scrollbar{
    display: none;
}
#aboutContent .expertlist li {
    display: grid;
    grid-template-columns: 50% 50%;
    padding: .5rem 0;
}
#aboutContent .expertlist .expertHead {
    width: 100%;
    height: 0;
    padding-top: 100%;
    background: #dddedf;
    border-top-right-radius: 35%;
    border-bottom-left-radius: 35%;
    position: relative;
    overflow: hidden;
}
#aboutContent .expertlist .expertInfo {
    padding: 1rem 0 1rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
#aboutContent .expertlist .expertInfo h3 {
    font-size: 1.3rem;
    font-weight: bolder;
    color: #fff;
    padding: .5rem 0;
}
#aboutContent .expertlist .expertInfo p{
    font-size: 1rem;
    padding: .5rem 0;
}
#aboutContent .patentlist li {
    padding: .5rem 0;
    position: relative;
}
#aboutContent .patentlist .patentInfo{
    padding: .5rem;
}
#aboutContent .patentlist .patentInfo h3 {
    font-size: 1.2rem;
    font-weight: bolder;
    color: #fff;
    padding: .5rem 0;
}
#aboutContent .patentlist .patentInfo p {
    font-size: 1rem;
    word-break:break-all;
}
#aboutContent .uniShow {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;
    position: relative;
    padding: 1rem 1rem;
}
#aboutContent .uniShow div {
    padding: .5rem;
}
#aboutContent .uniShow div span {
    display: inline-block;
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
    background-color: #dddedf;
    overflow: hidden;
}
#aboutContent .uniShow div:nth-child(1) span,
#aboutContent .uniShow div:nth-child(4) span {
    border-top-right-radius: 35%;
    border-bottom-left-radius: 35%;
}
#aboutContent .uniShow div:nth-child(2) span,
#aboutContent .uniShow div:nth-child(3) span{
    border-bottom-right-radius: 35%;
    border-top-left-radius: 35%;
}
#aboutContent .unilist {
    margin: 0 auto;
    border: 1px solid #64697a;
    border-radius: 10px;
}
#aboutContent .unilist li {
    line-height: 2rem;
    padding: 0 1rem;
    font-size: 1rem;
    text-align: center;
}
