.slideStage {
    width: calc(100% - 20px);
    padding: 0 10px;
}
.stage {
    width: 100%;
    overflow: scroll;
}
.stage::-webkit-scrollbar {
    display: none;
}
.slide {
    position: relative;
    display: flex;
    justify-content: space-around;
}

.scrollBar {
    height: 2px;
    margin: 1.5rem auto 0;
    background-color: #d5d5d5;
    position: relative;
}

.inner {
    background-color: #000;
    height: 2px;
    position: relative;
}