.imgbox {
    position: relative;
    display: block;
    height: 0;
    border-radius: 10px;
    background-color: rgb(192, 192, 192);
    overflow: hidden;
}

.imgbox label{
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 1.5rem;
    font-weight: bold;
    text-indent: 0;
    transform: translate(-50%, -50%);
    z-index: 10;
    color: #fff;
}

.pos-ab {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}