
.arrow_down_b {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 1px solid #606060;
    position: relative;
}

.arrow_b {
    width: 2rem;
    height: 2rem;
    position: absolute;
    top: 50%;
    left: 50%;
}

.a_r_b {
    border-top-left-radius: 100%;
    border-left: 2px solid #606060;
    border-top: 2px solid #606060;
}

.a_l_b {
    border-top-left-radius: 100%;
    border-left: 2px solid #606060;
    border-top: 2px solid #606060;
    transform: rotateY(-180deg);
    transform-origin: left;
}

.a_m_b {
    width: 2px;
    height: 4rem;
    background-color: #606060;
    transform: translate(-50%, -50%);
}

/* ArrowDownS */
.arrow_down_s {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    position: relative;
}

.arrow_s {
    width: 1.5rem;
    height: 1.5rem;
    position: absolute; 
    top: 50%;
    left: 50%;
}

.a_r_s {
    border-top-left-radius: 100%;
    border-left: 2px solid #fff;
    border-top: 2px solid #fff;
}

.a_l_s {
    border-top-left-radius: 100%;
    border-left: 2px solid #fff;
    border-top: 2px solid #fff;
    transform: rotateY(-180deg);
    transform-origin: left;
}

.a_m_s {
    width: 2px;
    height: 4rem;
    background-color: #fff;
    transform: translate(-50%, -50%);
}