.bus_show {
    width: 100%;
    position: relative;
    overflow: hidden;
    padding-top: 2rem;
}

.show_box {
    position: relative;
    width: 480px;
    margin: 0 auto;
    transform: translateX(-40px);
}

.show_box .row {
    position: relative;
    font-size: 0;
}

.show_box .hex {
    background-color: rgba(255, 220, 145, 0.7);
    display: inline-block;
    position: relative;
    clip-path: polygon(0 25%, 50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%);
    width: 110px;
    height: 120px;
    margin: 3px;
    border: 1px solid #e5e5e5;
}

.show_box .row:nth-child(2) {
    top: -30px;
    left: 60px;
}

.show_box .row:nth-child(3) {
    top: -60px;
}

.hex img {
    width: 90%;
    height: 90%;
    top: 50%;
    left: 50%;
    clip-path: polygon(0 25%, 50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%);
    transform: translate(-50%, -50%);
    border: 1px solid #a0a0a0;
}