.articleTitle {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color:rgba(179, 109, 30, 1);
    font-weight: 600;
    margin: 2rem 0;
    padding: 10px;
    margin-top: 4rem;
}
.articleTitle p{
    font-size: 1.5rem;
    margin-bottom: 1rem;
    opacity: 0.7;
}
.articleTitle h3{
    font-size: 'inherit';
    letter-spacing: .05rem;
}